import React, { Component } from 'react';
import { Button, Menu, Dropdown, Icon,Drawer, Input } from 'antd';
import Router from 'next/router'

import { connect } from 'react-redux'
import '../../static/css/app.css'


let {Search} = Input;

const menu = (
    <Menu>
        <Menu.Item key="0">
            <a href="http://www.alipay.com/">1st menu item</a>
        </Menu.Item>
        <Menu.Item key="1">
            <a href="http://www.taobao.com/">2nd menu item</a>
        </Menu.Item>
        <Menu.Divider />
        <Menu.Item key="3">退出登录</Menu.Item>
    </Menu>
);


class Navinfo extends Component {
    constructor(props) {
        super(props);
        this.state = {
            login: false,
            drawer: false,
            searchlist: []
        };
    }

    LinkPages = (href) => {

        Router.push(href)
    }

    onClose = (e) => {
        this.setState({
            drawer: false
        })
    }

    showDrawer = (e) => {
        this.setState({
            drawer: true
        })
    }

    searchList = (e) =>{
        console.log(e)
    }
    render() {
        let { login } = this.state;
        return (
            <div className="navinfo">
                <Button shape="circle" icon="search" onClick={this.showDrawer} />
                <Button>写推文</Button>
                {
                    login ? (<Dropdown overlay={menu} trigger={['click']}>

                        <img src="/static/img/123.jpg" className="avater" />
                    </Dropdown>) : <div className="unlogin">
                            <Button type="link" size={"small"} onClick={this.LinkPages.bind(this, '/')}>
                                登录
                    </Button>
                            <Button type="link" size={"small"} onClick={this.LinkPages.bind(this, '/')}>
                                注册
                    </Button>
                        </div>
                }

                <Drawer
                    title={<Search
                        placeholder="搜索更多内容"
                        onSearch={this.searchList}
                        style={{ width: 200 }}
                    />}
                    placement="bottom"
                    closable={false}
                    onClose={this.onClose}
                    visible={this.state.drawer}
                >
                    {
                        this.state.searchlist.map((value, i) => {
                            return (<p key={i}><a href={'/content/' + value._id} target="_blank" rel="noopener noreferrer">{value.title}</a></p>)
                        })
                    }
                </Drawer>
            </div>
        );
    }
}

export default Navinfo;